  //
  function getasyncRoutesMenu(routek, parent_title = "") {
    const res = []
    for (let index = 0; index < routek.length; index++) {
      const element = routek[index];
      let objck = {
        key: '',
        label: '',
      }
      if (!element.hidden) {
        if (element.meta && element.meta.title) {
          // let key = element.meta.key ? element.meta.key : element.meta.name ? element.meta.name : element.meta.title

          let key = ""
          if (element.meta.key) {
            key = element.meta.key
          } else {
            key = element.path
            if (key.substr(0, 1) == '/') {
              key = key.substr(1, key.length - 1)
            }
          }

          objck.key = parent_title + (parent_title != "" ? "&&" : '') + key
          objck.label = element.meta.title

        }
        if (element.children && !(hasOneShowingChild(element.children, element) && (!onlyOneChild
            .children || onlyOneChild.noShowingChildren) && !element.alwaysShow)) {
          objck.children = getasyncRoutesMenu(element.children, objck.key)
        } else {
          let children = [];
          if (typeof element.operating != "undefined") {
            for (
              let i = 0; i < element.operating.length; i++
            ) {
              const ele = element.operating[i];
              let obpj = {
                key: objck.key + "||" + ele.value,
                label: ele.label,
              }
              if (ele.selet_role) {
                obpj.selet_role = ele.selet_role
                if ((objck.key + "||" + ele.value) == neetrole) {
                  selet_role = ele.selet_role
                  break
                  // console.log(selet_role)
                  //  return selet_role
                }
              }
              children.push(obpj);
            }
            objck.children = children
          }
        }
        res.push(objck)
      }
    }
    //  console.log(selet_role)
    return selet_role
  }

  function hasOneShowingChild(children = [], parent) {
    const showingChildren = children.filter(item => {
      if (item.hidden) {
        return false
      } else {
        // Temp set(will be used if only has one showing child)
        onlyOneChild = item
        return true
      }
    })
    // When there is only one child router, the child router is displayed by default
    if (showingChildren.length === 1) {
      return true
    }

    // Show parent if there are no child router to display
    if (showingChildren.length === 0) {
      onlyOneChild = {
        ...parent,
        path: '',
        noShowingChildren: true
      }
      return true
    }

    return false
  }


  //
  function verdate(timew) {
    let currentdate = "";
    if (timew != null) {
      let char = "-";
      let date = new Date(timew);
      let day = date.getDate();
      let month = date.getMonth() + 1; //+1
      let year = date.getFullYear();
      //0
      currentdate =
        year +
        char +
        (month < 10 ? "0" + month : month) +
        char +
        (day < 10 ? "0" + day : day);
    }

    return currentdate;
  }
  //day
  function getDay(day, datec = new Date()) {
    //Date()
    var days = new Date(datec);
    //getTime() 1970  1  1 
    var gettimes = days.getTime() + 1000 * 60 * 60 * 24 * day;
    //setTime() Date 
    days.setTime(gettimes);
    var year = days.getFullYear();
    var month = days.getMonth() + 1;
    if (month < 10) {
      month = "0" + month;
    }
    var today = days.getDate();
    if (today < 10) {
      today = "0" + today;
    }
    return year + "-" + month + "-" + today;
  }

  //
  function countparameters(dataK) {
    let returnV = "";
    if (dataK != '[{"key":"","val":""}]') {
      // console.log(JSON.parse(dataK))
      try {
        dataK = JSON.parse(dataK);
        for (let index = 0; index < dataK.length; index++) {
          const element = dataK[index];
          if (element.key == "(KW)" && element.val != "") {
            returnV = returnV + element.val + "KW" + "/";
          } else if (element.key == "(rpm)" && element.val != "") {
            returnV = returnV + element.val + "rpm" + "/";
          } else if (element.key == "(Nm)" && element.val != "") {
            returnV = returnV + element.val + "Nm" + "/";
          } else if (element.key == "(rpm)" && element.val != "") {
            returnV = returnV + element.val + "rpm" + "/";
          }
        }
      } catch (error) {
        returnV = "";
      }
    }
    if (returnV != "") {
      returnV = returnV.substring(0, returnV.length - 1);
    }
    return returnV;
  }

  //month
  function getdatamonth(datec = new Date(), month) {
    datec = new Date(datec);
    // getMonth()0~11setMonth()
    datec.setMonth(datec.getMonth() - 1 + month);
    var yy1 = datec.getFullYear();
    var mm1 = datec.getMonth() + 1;
    //console.log(mm1);
    var dd1 = datec.getDate();
    if (mm1 < 10) {
      mm1 = "0" + mm1;
    }
    if (dd1 < 10) {
      dd1 = "0" + dd1;
    }
    return yy1 + "-" + mm1 + "-" + dd1;
  }

