  //tree
  /* 
  a:
  idStr:id
  pidStr:id
  chindrenStr:children
   */
  function transDatatotree(a, idStr, pidStr, chindrenStr) {
    const r = []
    const hash = {}
    const id = idStr
    const pid = pidStr
    const children = chindrenStr
    let i = 0
    let j = 0
    const len = a.length
    for (; i < len; i++) {
      hash[a[i][id]] = a[i]
    }
    for (; j < len; j++) {
      const aVal = a[j]
      const hashVP = hash[aVal[pid]]
      if (hashVP) {
        !hashVP[children] && (hashVP[children] = [])
        hashVP[children].push(aVal)
      } else {
        r.push(aVal)
      }
    }
    return r
  }

  /*  */
  function dealdepartment(searchList, res, key) {
    /*  */
    if (searchList !== "all") {
      for (let index = res.length - 1; index >= 0; index--) {
        const element = res[index];
        if (element.parent_id == null) {
          res.splice(index, 1)
        }
      }
    }
    let departmentArr = transDatatotree(res, "id", "parent_id", "children") //tree
    //null
    if (searchList === null || searchList === "all") {
      return departmentArr
    } else {
      let ink = searchList.findIndex(i => i.key === key)
      if (ink == -1) {
        return false
      } else {
        return {
          ink: ink,
          departmentArr: departmentArr
        }
      }
    }
  }

  //
  function formatDate(date, fmt) {
    //  console.log(typeof date == 'undefined')
    if (date == '' || date == null || typeof date == 'undefined') {
      fmt = "";
    } else {
      date = new Date(date);
      if (/(y+)/.test(fmt)) {
        fmt = fmt.replace(
          RegExp.$1,
          (date.getFullYear() + "").substr(4 - RegExp.$1.length)
        );
      }
      let o = {
        "M+": date.getMonth() + 1,
        "d+": date.getDate(),
        "h+": date.getHours() % 12 == 0 ? 12 : date.getHours() % 12, //
        "H+": date.getHours(), //
        "m+": date.getMinutes(),
        "s+": date.getSeconds()
      };
      for (let k in o) {
        if (new RegExp(`(${k})`).test(fmt)) {
          let str = o[k] + "";
          fmt = fmt.replace(
            RegExp.$1,
            RegExp.$1.length === 1 ? str : padLeftZero(str)
          );
        }
      }
    }
    return fmt;
  }

  /*  */
  function formatroute(path) {
    let comdata = ""
    let arrA = path.split("/")
    let flag = 0
    for (let index = 0; index < arrA.length; index++) {
      if (arrA[index] != '') {
        if (silderlist.includes('/' + arrA[index])) {
          /*  */
          comdata = arrA[index]
          break
        }
        comdata = comdata + (comdata != '' ? '/' : '') + arrA[index]
        flag++
        if (flag == 2) {
          break
        }
      }
    }
    // console.log(comdata)
    return ("/" + comdata)
  }
  //
  function changeformatDate(date) {
    date = String(date)
    let str = ''
    let arr = date.split("-")
    let time = formatDate(new Date(), "yyyy-MM-dd").split("-")
    if ((arr[0] == time[0] && arr[1] == time[1])) {
      str = arr[2] + ""
    } else {
      str = arr[1] + "" + arr[2] + ""
    }
    return str
  }

  function padLeftZero(str) {
    return ("00" + str).substr(str.length);
  }

