  function getnowdate() {
    var date = new Date();
    var seperator1 = "-";
    var year = date.getFullYear();
    var month = date.getMonth() + 1;
    var strDate = date.getDate();
    var currentdate = year + "" + month + "" + strDate + "";
    return currentdate;
  }

  function getCurrentDateStr() {
    var now = new Date();
    var year = now.getFullYear();
    var month = now.getMonth() + 1;
    var strDate = now.getDate();
    var hh = now.getHours(); //
    var mm = now.getMinutes(); //
    var ss = now.getSeconds(); //
    //2018-02-03 00:00:00
    var currentdate =
      year + "-" + month + "-" + strDate + " " + hh + ":" + mm + ":" + ss;
    return currentdate;
  }

  //
  function countTimeDifference(date1, date2) {
    let date3 = new Date(date2).getTime() - new Date(date1).getTime(); //
    let days = Math.floor(date3 / (24 * 3600 * 1000));
    //
    let leave1 = date3 % (24 * 3600 * 1000); //
    let hours = Math.floor(leave1 / (3600 * 1000));
    //
    let leave2 = leave1 % (3600 * 1000); //
    let minutes = Math.floor(leave2 / (60 * 1000));
    //
    let leave3 = leave2 % (60 * 1000); //
    let seconds = Math.round(leave3 / 1000);

    return {
      days: days,
      hours: hours,
      minutes: minutes,
      seconds: seconds
    };
  }


  /* 
  
  value   --
  option  --

   */
  function getCascaderFullValue(value, option, key = "label") {
    let optionFullValue = [];
    _getParentId(optionFullValue, option, option, value);
    if (key == "label") {
      return optionFullValue.join('/');
    } else {
      return optionFullValue
    }
    /**
     * @param {Array} optionFullValue value for return
     * @param {Array} option value for loop
     * @param {Array} _option value of original
     * @param {number} _value  value of bottom item
     */
    function _getParentId(optionFullValue, option, _option, _value) {
      // console.log(_value)
      if (!Array.isArray(option)) return;

      let indexk = option.findIndex(item => item.value === _value);
      if (indexk >= 0) {
        optionFullValue.push(option[indexk][key])
      } else {
        for (let optionItem of option) {
          //if (!Array.isArray(optionItem.children)) continue;
          if (Array.isArray(optionItem.children)) {
            let index = optionItem.children.findIndex(item => item.value === _value);
            if (index >= 0) {
              optionFullValue.push(optionItem[key]);
              optionFullValue.push(optionItem.children[index][key]);
              if (_option.findIndex(item => item.value === optionItem.value) < 0) //is original item
                _getParentId(optionFullValue, _option, _option, optionItem.value);
            } else {
              _getParentId(optionFullValue, optionItem.children, _option, _value);
            }
          } else {
            // let index = option.findIndex(item => item.value === _value);
            // if (index >= 0) {
            //  // console.log(option[index][key])
            //   optionFullValue.push(option[index][key])
            // }
          }
        }
      }

    }
  }




