      getdatadetail(id, api) {
        return new Promise(resolve => {
          let allparams = {
            params: {
              id: id
            },
            api: api,
          }
          store
            .dispatch("getmsgData", allparams)
            .then(response => {
              // this.loading = false;
              let res = response.data;
              let row = null
              if (ifrrandnotnone(res.results)) {
                if (res.results.length == 1) {
                  row = res.results[0]
                } else if (res.results.length > 1) {
                  row = res.results
                  Vue.prototype.$message.warning("" + res.results.length + '');
                } else {
                  Vue.prototype.$message.warning("");
                  Vue.prototype.$eventBus.$emit("to404")
                }
              } else {
                if (res.length == 1) {
                  row = res[0]
                } else {
                  Vue.prototype.$message.warning("");
                  Vue.prototype.$eventBus.$emit("to404")
                }
              }

              resolve(row)
            })
            .catch(err => {
              resolve(null)
              console.log(err);
              Vue.prototype.$message.error(",:" + api);
            });
        })
      },



