//
function addlogs(response, url) {
    let method = response.config.method
    if (method != "get") {
        //
        //console.log(url)
        if (url == 'login' && response.data.status == 'success') {
            //
            localStorage.setItem(
                "UserMsg",
                JSON.stringify(response.data.msg.usermsg)
            );
        } else if (url == 'login' && response.data.status == 'fail') {
            return response
        }
        //localstoage
        if (url == 'users') {
            if (response.data.id == JSON.parse(
                localStorage.getItem("UserMsg")
            ).id) {
                commom.methods.getapidata("users", {
                    id: response.data.id
                })
            }
        }
        // console.log(url, 'url')
        let item = commom.apiinterfaceManArr.find(item => item.api_name === url)
        if (typeof item != 'undefined') {
            console.log(item, method, ' ')
            //
            let params = response.config.params
            let operation_event = item.op_name
            if (!item.if_single || typeof item.if_single == 'undefined') {
                operation_event = (method == "get" ? '' : method == "post" ? '' : method == "patch" ? '' : method == 'put' ? '' : method == 'delete' ? '' : '') + item.op_name
            }
            // console.log(operation_event)
            axios.post(urltit + "systemLogs" + "/", {
                operation_user_id: JSON.parse(
                    localStorage.getItem("UserMsg")
                ).id,
                operation_user_name: JSON.parse(
                    localStorage.getItem("UserMsg")
                ).username,
                operation_event: operation_event,
                operation_obj: JSON.stringify({
                    url: url,
                    id: response.data.id
                })
            })
                .then(res => {
                    //  console.log(res);
                })
        }
    }
}

//
function getdatachange(response, url) {
    let res = response.data

    let results = typeof res.results == 'undefined' ? res : res.results
    /*  */
    if (Object.prototype.toString.call(results) === "[object Array]" && results.length > 0) {
        let tableData = []
        for (let index = 0; index < results.length; index++) {
            const element = results[index];
            // console.log(element.edit_by)
            /*  */
            if (typeof element.edit_by_obj != 'undefined') {
                if (element.edit_by_obj != null) {
                    element.edit_by = element.edit_by_obj
                } else {
                    element.edit_by = {}
                }
            }

            tableData.push(element)
        }
        if (typeof res.results == 'undefined') {
            response.data = tableData
        } else {
            response.data.results = tableData
        }
    }

    return response
}


